@import url(https://fonts.googleapis.com/css2?family=Gruppo&family=Noto+Sans+Display&family=Play:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Play:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{box-sizing:border-box;margin:0;padding:0}.App{display:flex;height:100%;background-color:#e6e6fa;overflow:auto}.App__nav{position:fixed;height:100vh;border:2px solid #000}.App__content{width:90rem;margin-left:18.3%;height:100%;text-align:center;border:2px solid blue}@media screen and (max-width: 768px){.App{display:inline-block;width:100vw}.App__nav{display:flex;justify-content:center;text-align:left;width:inherit}.App__content{width:inherit}}
.card{display:inline-block;padding:0px;margin:10px;width:20rem;height:39rem;background:linear-gradient(to right, #cfd2f3, #e0eafc);box-shadow:rgba(0,0,0,.14) 2px 2px 15px 1px;border-radius:20px;overflow:auto}.card__img{width:5rem;height:20rem}.card__heading{display:block}.card__name{margin:1em 0px 0px;color:#36393d;font-size:2em;font-weight:bold;font-family:"Gruppo",cursive}.card__tagline{margin:0px;font-size:1.25em;font-weight:bold;font-family:"Noto Sans Display",sans-serif}.card__figures{margin:1em 0px;display:block}.card__description{text-align:left;font-size:.86em;border-radius:5px;font-weight:600;line-height:1.4em;overflow:auto;color:purple;padding-right:10px;padding-left:10px;height:7rem}.card__figure{display:inline-block;background-color:#cfd2f3;border-radius:15px;margin-right:1.5em;padding:.5em .3em;min-width:50px;font-size:.7em;margin-bottom:0}.card__figure-label{text-align:center;letter-spacing:2px;opacity:.75}.card__figure-value{font-size:1.75em;text-align:center;font-weight:bold;margin:0px}
.cardList{display:grid;grid-template-columns:repeat(3, 1fr);justify-content:space-evenly}@media screen and (max-width: 768px){.cardList{grid-template-columns:1fr}}
.main{display:inline-block}.main__heading{font-family:"Play",sans-serif;display:inline-block;margin-bottom:20px}.main__heading-title{font-family:"Play",sans-serif;margin:.5em 0px 0px;font-size:50px}@media screen and (max-width: 768px){.main__heading-title{font-weight:200px;font-size:x-large}.main__heading-subheading{font-style:italic;font-weight:50;font-size:medium}}

.filters{display:inline-block;width:15rem;line-height:1.6em}
.navbar{display:inline-block;margin-left:20px}.navbar__searchbox{position:relative;width:80%;margin:20px 0;padding:0}.navbar__searchbox-input{position:relative;width:100%;border:none;border:1px solid gray;border-radius:5px;padding:10px 20px;color:gray}.navbar__searchbox-label{display:inline-block;padding:0 5px;font-size:5px;transition:all .4s ease;-webkit-transform:translateX(19px) translateY(-64px);transform:translateX(19px) translateY(-64px);opacity:1;background-color:#fff}
